import React, { Component } from "react";
import { BarChart } from "react-easy-chart";
import "./App.css";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      roomsRange: 50,
      averages: Array(50)
        .fill(0)
        .map((v, i) => {
          return { x: i, y: Math.random() * 100 };
        }),
      iterations: 1,
      loop: true
    };
    this.sims = [];
    this.averagesAcc = [Array(50).fill(1)];
  }
  updateAverages(newAverages) {
    console.log(newAverages);
    const averages = newAverages.map((val, i) => {
      return {
        x: i,
        y: (val - 1) * 100
      };
    });
    setTimeout(() => {
      this.setState({ averages }, () => {
        if (this.state.loop) {
          this.simRoomsThenAverage(this.state.iterations);
        }
      });
    }, 750);
  }
  simRoomsThenAverage() {
    const iterations = this.state.iterations;
    for (let i = 0; i < iterations; i++) {
      const newSim = Array(this.state.roomsRange)
        .fill(0)
        .map((v, size) => {
          const matchez = Array(365).fill(0);
          for (let i = 0; i < size; i += 1) {
            let b = Math.floor(Math.random() * 365);
            matchez[b] += 1;
          }
          return matchez.reduce((match, day) => (match || day > 1 ? 1 : 0), 0);
        });
      const oldAverages = this.averagesAcc.slice(-1)[0];
      const newAverages = oldAverages.map((oldAverage, size) => {
        const sizeNew = this.averagesAcc.length + 1;
        const newValue = newSim[size] + 1;
        const newAverage =
          oldAverage === 0
            ? newValue
            : oldAverage + (newValue - oldAverage) / sizeNew;
        return newAverage;
      });
      this.sims.push(newSim);
      this.averagesAcc.push(newAverages);
    }
    this.updateAverages(this.averagesAcc.slice(-1)[0]);
  }
  handleIterations(e) {
    if (e.target.value > 5000) {
      return;
    } else {
      this.setState({ iterations: e.target.value });
    }
  }
  render() {
    console.log(this.state.averages);
    return (
      <div className="App">
        <div> Birthday Paradox</div>
        <span>Iterations: {this.averagesAcc.length - 1}</span>
        <BarChart
          axes
          colorBars
          axisLabels={{ x: "People", y: "Matching Birthdays" }}
          height={1000}
          width={1300}
          data={this.state.averages}
          lineData={this.state.averages}
          interpolate="cardinal"
        />
        <input
          type="number"
          onChange={e => this.handleIterations(e)}
          value={this.state.iterations}
        />
        <button onClick={() => this.simRoomsThenAverage()}>simulate</button>
        <br />
        <span>loop</span>
        <input
          type="checkbox"
          onChange={() => this.setState(prev => ({ loop: !prev.loop }))}
          checked={this.state.loop}
        />
      </div>
    );
  }
}

export default App;
